.modal-overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(30, 48, 97, 0.6);
  opacity: 0;
  z-index: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}

.modal-overlay.active {
  visibility: visible;
  opacity: 1;
  z-index: 12;
}

.modal-overlay .close-btn {
  width: 62px;
  height: 62px;
  background-color: white;
  border-radius: 100%;
  position: absolute;
  transform: rotate(-45deg);
  cursor: pointer;
}

.modal-overlay .close-btn:after, .modal-overlay .close-btn:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 1px;
  width: 20px;
  background-color: #3d3d3d;
  transition: 0.3s ease;
}

.modal-overlay .close-btn:before {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.modal-overlay .close-btn:hover:after, .modal-overlay .close-btn:hover:before {
  background-color: #ffb757 !important;
}

