  display: none;
}

/*

   POSITIONING
   Docs: http://tachyons.io/docs/layout/position/

   Media Query Extensions:
     -ns = not-small
     -m  = medium
     -l  = large

*/
.static {
  position: static;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

header.fixed-header {
  height: 60px;
}

header.fixed-header .header-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
}

img {
  max-width: 100%;
}

.header-container {
  width: 100%;
}

.header-container .container {
  height: 60px;
  text-align: right;
  overflow: visible;
}

.header-container .header-logo, .header-container .header-logo-dark {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.header-container .header-logo-dark {
  display: none;
}

.header-container .header-menu {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.header-container .header-menu a {
  display: inline-block;
  vertical-align: middle;
}

.header-container .header-menu a:last-child li {
  padding-right: 0px;
}

.header-container .header-menu a li {
  color: white;
  padding: 10px 16px;
  padding-top: 12px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
}

.header-container .header-menu a li button {
  margin-top: -2px;
  border: 1px solid #ffb757;
  color: #ffb757;
  text-transform: uppercase;
}

@media (max-width: 650px) {
  .header-container .header-menu {
    display: none;
  }
}

.header-container .header__profile--menu a {
  display: block;
}

.header-container.solid {
  background-color: white;
  box-shadow: inset 0 -1px 0 0 #dfe4e8;
}

.header-container.solid .header-logo-dark {
  display: block;
}

.header-container.solid .header-logo {
  display: none;
}

.header-container.solid .header-menu a {
  text-decoration: none;
}

.header-container.solid .header-menu a li {
  color: #3d3d3d;
  transition: 0.3s ease;
}

.header-container.solid .header-menu a li:hover {
  color: #ffb757;
}

.header__login {
  display: inline-block;
  position: relative;
  cursor: pointer;
  padding-left: 8px;
}

.header__login:before {
  content: '';
  position: absolute;
  top: 50%;
  left: -8px;
  transform: translateY(-50%);
  width: 1px;
  height: 16px;
  background-color: #e4e4e4;
}

.header__login span {
  margin: 0 8px;
  padding-bottom: 3px;
  color: #a4a4a4;
  font-size: 12px;
  vertical-align: middle;
}

.header__profile {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  vertical-align: middle;
}

.header__profile img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.header__profile--menu {
  position: absolute;
  top: 175%;
  right: -45px;
  width: 130px;
  transform: translateY(-20%);
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 3px 0 rgba(63, 63, 68, 0.15), 0 0 0 1px rgba(63, 63, 68, 0.05);
  padding: 4px 0;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;
  z-index: 1;
}

.header__profile--menu .btn {
  width: 100%;
}

.header__profile--menu a {
  padding: 8px;
  text-align: left;
  color: #3d3d3d;
  transition: 0.3s ease;
  border-bottom: 1px solid #e4e4e4;
}

.header__profile--menu a:hover {
  color: #ffb757;
}

.header__profile--menu.active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.header__profile--menu:after, .header__profile--menu:before {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.header__profile--menu:after {
  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #fff;
  border-width: 10px;
  margin-left: -10px;
}

.header__profile--menu:before {
  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #fff;
  border-width: 11px;
  margin-left: -11px;
}

.header__logout {
  padding: 4px 4px 0;
}

.header-hamburger {
  display: none;
}

@media (max-width: 650px) {
  .header-hamburger {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
  }
  .header-hamburger span {
    font-size: 24px;
  }
}

footer {
  position: relative;
  z-index: 7;
}

.footer-container {
  width: 100%;
  background-color: #282834;
  padding: 38px 0px;
}

.footer-container .footer-body {
  padding-top: 32px;
  padding-bottom: 22px;
}

.footer-container .footer-body ul {
  display: inline-block;
  width: 100%;
}

.footer-container .footer-body ul li {
  display: block;
  margin-bottom: 12px;
  font-size: 12px;
  letter-spacing: 1px;
  text-align: left;
  color: #a4a4a4;
  line-height: 1.25em;
  transition: 0.3s ease;
}

.footer-container .footer-body ul > li {
  color: #ffffff;
  letter-spacing: 2px;
}

.footer-container .footer-body ul a:hover li {
  color: #ffb757;
}

@media (max-width: 768px) {
  .footer-container .footer-body .col-md-2 {
    padding-left: 8px;
    padding-right: 8px;
  }
  .footer-container .footer-body .col-md-2 span {
    display: block;
    margin-bottom: 4px;
  }
}

@media (max-width: 580px) {
  .footer-container .footer-body {
    padding-bottom: 8px;
  }
  .footer-container .footer-body .col-md-2 {
    padding-left: 15px;
    padding-right: 15px;
  }
  .footer-container .footer-body .col-md-2 span {
    display: inline-block;
    margin-bottom: 0;
  }
  .footer-container .footer-body .col-md-4 {
    margin-top: 42px;
  }
  .footer-container .footer-body .col-md-4 ul, .footer-container .footer-body .col-md-4 li {
    margin: 0;
  }
  .footer-container .footer-body .col-md-4 li {
    margin-bottom: 4px;
  }
  .footer-container .footer-body ul li {
    text-align: center;
  }
}

.footer-container .footer-breadcrumbs {
  position: relative;
  padding-top: 32px;
  border-top: solid 1px #3a3a4b;
}

@media (max-width: 580px) {
  .footer-container .footer-breadcrumbs {
    padding-bottom: 32px;
  }
}

.footer-container .copyright {
  font-size: 12px;
  font-weight: 400;
  color: #a4a4a4;
}

@media (max-width: 580px) {
  .footer-container .copyright {
    position: absolute;
    bottom: 0;
    text-align: center;
  }
}

.footer-container .social-media-container {
  display: inline-block;
  white-space: nowrap;
}

.footer-container .social-media-container .social-media-circle {
  display: inline-block;
  width: 36px;
  height: 36px;
  background-color: #3a3a4b;
  border-radius: 50%;
  color: white;
  text-align: center;
  cursor: pointer;
  margin-left: 21px;
  transition: 0.3s ease;
}

.footer-container .social-media-container .social-media-circle span {
  line-height: 40px;
}

.footer-container .social-media-container .social-media-circle:hover {
  color: #ffb757;
}

@media (max-width: 580px) {
  .footer-container .social-media-container {
    display: block;
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 580px) {
  .footer-container .col-sm-12 {
    text-align: center;
  }
}

.btn {
  background-color: transparent;
  border: 0px solid transparent;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  padding: 12px 8px 4px 8px;
  border-radius: 4px;
  background-color: #ffffff;
  border: solid 1px #a4a4a4;
  cursor: pointer;
  text-transform: uppercase;
  font-family: 'Hind Guntur', sans-serif;
  position: relative;
}

.btn.small {
  padding: 12px 20px 4px 20px;
}

.btn.transparent {
  background-color: transparent;
}

.btn:active {
  transform: translateY(1px);
  outline: none;
}

.btn:focus {
  outline: none;
}

.btn.btn--medium {
  padding: 16px 16px 10px 16px;
  font-size: 15px;
}

.btn.btn--blue {
  background-color: #5b8ef4;
  border: solid 1px #4d7cda;
  color: white;
}

.btn.btn--orange {
  background-color: #ffb757;
  border: solid 1px #f0a643;
  color: white;
}

.btn.btn-add, .btn.btn-add-2 {
  border: 1px #5b8ef4;
  border-style: dashed;
  background-color: white;
  font-size: 15px;
  font-weight: 600;
  line-height: 1;
  padding: 10px 16px 10px 16px;
}

.btn.btn-add .ico--plus, .btn.btn-add-2 .ico--plus {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-color: #5b8ef4;
  vertical-align: middle;
  border-radius: 50%;
  margin-right: 8px;
  position: relative;
}

.btn.btn-add .ico--plus:after, .btn.btn-add .ico--plus:before, .btn.btn-add-2 .ico--plus:after, .btn.btn-add-2 .ico--plus:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 1px;
  width: 8px;
  background-color: white;
}

.btn.btn-add .ico--plus:before, .btn.btn-add-2 .ico--plus:before {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.btn.btn-add .btn--text, .btn.btn-add-2 .btn--text {
  display: inline-block;
  margin: 0px;
  vertical-align: middle;
  margin-top: 2px;
}

.btn.btn-add-2 {
  background-color: #5b8ef4;
  border: solid 1px #4d7cda;
}

.btn.btn-add-2 .btn--text {
  color: white;
}

.btn.btn-add-2 .ico--plus {
  background-color: white;
}

.btn.btn-add-2 .ico--plus:before, .btn.btn-add-2 .ico--plus:after {
  content: '';
  background-color: #5b8ef4;
}

.card {
  display: inline-block;
  width: 100%;
  margin: 0 10px;
  box-shadow: 0 1px 3px 0 rgba(63, 63, 68, 0.15), 0 0 0 1px rgba(63, 63, 68, 0.05);
}

.card:hover .card__pricewrap img circle, .card:hover .card__pricewrap svg circle {
  fill: #5b8ef4;
  stroke: #fff;
}

.card:hover .card__pricewrap img path, .card:hover .card__pricewrap svg path {
  stroke: #fff;
}

.card--full {
  width: 100%;
  margin: 0;
  transition: transform 0.3s ease;
}

.card--full .card__slider {
  height: 250px;
}

@media (max-width: 1100px) {
  .card--full .card__slider {
    height: 200px;
  }
}

.card--full:hover {
  transform: translateY(-10px);
  box-shadow: 0px 6px 10px 0px rgba(63, 63, 68, 0.15);
}

.card__slider {
  position: relative;
  width: 100%;
  height: 200px;
}

.card__slider img {
  width: 100%;
  height: 100%;
}

.card__location {
  padding: 15px 0;
  margin: 0;
}

.card__map {
  padding-right: 0;
}

.card__map .pd-maps-box {
  height: 110px;
}

@media (max-width: 580px) {
  .card__map {
    padding-left: 8px;
  }
}

.card__infowrap h5 {
  font-weight: bold;
  margin-bottom: 15px;
}

.card__infowrap .card__desc {
  color: #637381;
  padding-top: 2px;
  height: 45px;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 580px) {
  .card__infowrap .card__desc {
    margin: 0;
    line-height: 15px;
    height: auto;
    max-height: 75px;
  }
}

@media (max-width: 580px) {
  .card__infowrap {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.card__info {
  color: #a4a4a4;
}

@media (max-width: 580px) {
  .card__info {
    display: none;
  }
}

.card__info span, .card__info p {
  display: inline-block;
  color: #a4a4a4;
}

.card__info span {
  margin-right: 5px;
}

.card__info--mobile {
  display: none;
  padding-top: 15px;
  height: 30px;
}

@media (max-width: 580px) {
  .card__info--mobile {
    display: block;
  }
}

.card__pricewrap {
  width: 100%;
  padding: 15px;
  border-top: solid 1px #e4e4e4;
  border-bottom: solid 1px #e4e4e4;
}

.card__pricewrap h2, .card__pricewrap h3 {
  color: #ffb757;
  margin: 0;
  font-weight: bold;
}

.card__pricewrap p {
  margin: 0 0 10px;
  font-weight: bold;
}

.card__pricewrap span {
  color: #637381;
  font-size: 15px;
  vertical-align: middle;
  font-weight: normal;
}

.card__pricewrap img, .card__pricewrap svg {
  position: absolute;
  width: 35px;
  height: 35px;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.card__pricewrap--row {
  border: none;
  height: 100%;
}

.card__price-middle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 568px) {
  .card__price-middle {
    position: relative;
    top: auto;
    transform: none;
  }
}

.card__modalinfo {
  padding: 15px;
}

.card__modallocation {
  margin: 0 -15px;
}

@media (max-width: 568px) {
  .card__modallocation {
    margin: 0;
  }
}

.card__modallocation-content img, .card__modallocation-content svg {
  display: inline-block;
}

.card__modallocation-content span {
  vertical-align: text-top;
  font-size: 14px;
  color: #a4a4a4;
}

@media (max-width: 568px) {
  .card__modallocation-content {
    padding: 0;
  }
}

.card__row .card__desc {
  height: 29px;
  padding-right: 10px;
}

.general--modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 999;
