.radio-area label {
  display: block;
  height: 40px;
  position: relative;
  padding-top: 14px;
  padding-bottom: 15px;
  cursor: pointer;
  padding-left: 40px;
}

.radio-area label:before {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: white;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #a4a4a4;
}

.radio-area label:after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #5c8ef3;
  left: 5px;
  top: 50%;
  /*will-change: transform  ;*/
  transform-origin: center;
  transform: translateY(-50%) scale(0);
  opacity: 0;
  transition: 0.4s ease;
}

.radio-area label.active:after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #5c8ef3;
  left: 5px;
  top: 50%;
  transform: translateY(-50%) scale(1);
  opacity: 1;
  transition: 0.4s ease;
}

.checkbox-area input {
  display: none;
