.checkbox-area label {
  display: block;
  height: 40px;
  position: relative;
  padding-top: 14px;
  padding-bottom: 15px;
  cursor: pointer;
  padding-left: 40px;
}

.checkbox-area label:before {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: white;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #a4a4a4;
  border-radius: 3px;
  transition: 0.4s ease;
}

.checkbox-area label:after {
  content: "";
  position: absolute;
  width: 8px;
  height: 14px;
  border-right: 3px solid white;
  border-bottom: 3px solid white;
  left: 8px;
  top: 45%;
  transform-origin: center;
  transform: translateY(-50%) rotate(0deg) scale(0);
  opacity: 0;
  transition: 0.4s ease;
}

.checkbox-area label.active:before {
  background-color: #5b8ef4;
  border: 1px solid #5b8ef4;
}

.checkbox-area label.active:after {
  content: "";
  position: absolute;
  transform: translateY(-50%) rotate(45deg) scale(1);
  opacity: 1;
  transition: 0.4s ease;
}

