  width: 100%;
  height: 100%;
  color: #fff;
}

.static__hiw a:hover {
  text-decoration: none;
  outline: none;
}

.static__hiw span {
  color: #fff;
  margin-right: 4px;
}

.static__hiw:hover {
  opacity: 0.8;
}

@media (max-width: 580px) {
  .static__hiw {
    text-align: center;
  }
  .static__hiw a {
    padding: 32px 0;
  }
}

.static__hiw-imagewrap {
  margin-bottom: 32px;
}

.static__hiw-imagewrap img {
  width: 100%;
}

.static__hiw-benefit {
  margin-top: 48px;
  padding: 32px 0;
  margin-bottom: 0;
}

.static__hiw-imagewrap--content, .static__hiw-benefit--sides {
  padding-left: 8px;
  padding-right: 8px;
}

.static__hiw-benefit--content {
  margin-bottom: 32px;
}

.static__what-is {
  position: relative;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin-bottom: 16px;
}

.static__what-is:before {
  content: '';
  display: block;
  padding-top: 33.3333%;
}

.static__what-is img, .static__what-is svg {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 185px;
  height: 100px;
  transform: translate(-50%, -50%);
}

@media (max-width: 580px) {
  .static__what-is:before {
    padding-top: 75%;
  }
}

.static__contact-maps, .static__contact-sent {
  position: relative;
  margin: 64px 0 32px;
}

.static__contact-maps .col-md-5, .static__contact-sent .col-md-5 {
  padding-left: 0;
  padding-right: 8px;
}

.static__contact-maps .col-md-7, .static__contact-sent .col-md-7 {
  padding-left: 8px;
  padding-right: 0;
}

.static__contact-sent {
  margin: 0;
  border-bottom: 1px solid #e4e4e4;
}

.static__pointer {
  margin-bottom: 16px;
}

.static__contact-info {
  margin-top: 100px;
}

.static__contact-info p {
  margin-bottom: 32px;
  color: #fff;
}

@media (max-width: 580px) {
  .static__contact-info {
    margin-top: 30px;
  }
}

.static__maps {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  width: 100%;
  height: 275px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.18);
}

.static__maps.static__maps--mobile {
  display: none;
}

@media (max-width: 580px) {
  .static__maps.static__maps--desktop {
    display: none;
  }
  .static__maps.static__maps--mobile {
    display: block;
    position: relative;
    top: auto;
    transform: none;
    margin-left: -16px;
    width: calc(100% + 40px);
  }
}

.static_contact-bg {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 225px;
  background-color: #5b8ef4;
  z-index: 1;
}

.static__input {
  margin-bottom: 32px;
}

.static__contact-button {
  padding-top: 32px;
}

.static__contact-button button {
  width: 150px;
}

@media (max-width: 580px) {
  .static__contact-button {
    text-align: center;
  }
}

.static__faq {
  width: 100%;
  padding: 16px 0;
  border-bottom: 1px solid #e4e4e4;
  cursor: pointer;
}

.static__faq.active .static__faq-head span {
  transform: translateY(-50%) rotate(-180deg);
}

.static__faq.active .static__faq-body {
  display: block;
  transform: translateY(0px);
}

@media (max-width: 580px) {
  .static__faq.active .static__faq-body {
    padding-top: 14px;
  }
}

.static__faq-head {
  position: relative;
  padding-left: 55px;
}

.static__faq-head:before {
  content: attr(number);
  position: absolute;
  top: 50%;
  left: 13px;
  transform: translateY(-50%);
  font-size: 18px;
  display: inline-block;
}

.static__faq-head:after {
  content: '';
  position: absolute;
  top: 40%;
  left: 0;
  transform: translateY(-50%);
  border-radius: 50%;
  border: 1px solid #e4e4e4;
  width: 32px;
  height: 32px;
  display: inline-block;
}

.static__faq-head.static__faq-head--more:before {
  top: 9px;
  left: 9px;
}

@media (max-width: 400px) {
  .static__faq-head.static__faq-head--more:before {
    top: 16px;
  }
}

.static__faq-head span {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  border-radius: 50%;
  padding: 4px;
  font-size: 15px;
  background-color: #F9F9FB;
  color: #B6B6B6;
  transition: transform 0.3s ease;
  will-change: transform;
}

.static__faq-body {
  display: none;
  padding-left: 55px;
}

@media (max-width: 580px) {
  .static__faq-body {
    padding-left: 0;
  }
}

.static-breadcrumpt {
  display: none;
}

@media (max-width: 770px) {
  .static-breadcrumpt {
    display: block;
    background-color: #5b8ef4;
    padding: 12px 16px;
  }
  .static-breadcrumpt a, .static-breadcrumpt b {
    color: #fff;
  }
}

.error {
  position: relative;
  height: calc(100vh - 60px);
  background-color: #f9f9fb;
}

.error_wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
}

.error_wrapper img {
  width: 100%;
  margin-bottom: 52px;
}

.error_wrapper p {
  width: 450px;
  max-width: 100%;
  margin: 24px auto 32px;
}

.error_wrapper button {
  width: 200px;
}

@media (max-width: 1024px) {
  .error_wrapper {
    width: 60%;
  }
}

@media (max-width: 568px) {
  .error_wrapper {
    width: 90%;
  }
  .error_wrapper img {
    margin-bottom: 24px;
  }
  .error_wrapper p {
    margin: 16px auto 24px;
  }
}
